@import "../../Styles/scss/style";

.basket-window {
    display: flex;
    flex-direction: column;
    max-width: rem(750);
    width: 100%;
    height: rem(550);
    height: clamp(250px, 85vh, 550px);
    background-color: #fff;
    overflow-y: auto;
    @media (max-width: $mobile) {
        max-width: 100%;
        height: 100%;
    }
}
.top-basket-window {
    display: flex;
    align-items: center;
    padding-left: rem(12);
    box-sizing: border-box;
    border-bottom: 1px solid #e9e9e9;
    &__title {
        font-size: 20px;
        font-weight: 700;
        color: #34302e;
        flex: 1 1 auto;
    }
    &__close {
        display: flex;
        align-items: center;
        justify-content: center;
        width: rem(50);
        height: rem(50);
        color: #797878;
        cursor: pointer;
        @media (any-hover: hover) {
            &:hover {
                color: #000;
            }
        }
        & svg,
        & path {
            width: rem(18);
            height: rem(18);
            stroke: currentColor;
            transition: all 0.2s ease;
        }
    }
}
.body-basket-window__cont{
    overflow-y: auto;
}
.body-basket-window__cont,
.body-basket-window__wrapper{
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
}
.item-basket-window {
    display: grid;
    align-items: center;
    column-gap: rem(10);
    grid-template-columns: max-content 4fr 1fr 2fr max-content;
    padding: rem(10) rem(16) rem(10) rem(0);
    &__img {
        width: rem(80);
        height: rem(80);
    }
    &__quantity{
        width: 30px;
        height: 30px;
        text-align: center;
        border: rem(1) solid #e9e9e9;
    }
    &__more {
        position: relative;
        width: rem(20);
        height: rem(20);
        display: flex;
        align-items: center;
        justify-content: center;
        color: #797878;
        user-select: none;
        cursor: pointer;
        @media (any-hover: hover) {
            &:hover {
                color: #000;
            }
        }
        & > svg,
        & > path {
            width: rem(18);
            height: rem(18);
            stroke: currentColor;
            transition: all 0.2s ease;
        }
    }
    &__delete {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        display: flex;
        align-items: center;
        column-gap: rem(5);
        width: 250px;
        padding: 16px 16px;
        border-radius: 4px;
        background-color: #fff;
        box-shadow: 0 2px 4px #00000030;
        color: $accentColor;
        z-index: 1;
        @media (any-hover: hover) {
            &:hover {
                color: $red;
            }
        }
        & > svg,
        & > path {
            width: rem(18);
            height: rem(18);
            stroke: currentColor;
        }
    }
}
.footer-basket-window{
    padding: rem(20) rem(12) rem(20) rem(12);
    margin-top: auto;
    border-top: 1px solid #e9e9e9;
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 18px;
    }
    &__all{
    }
    &__box{
        margin-top: rem(30);
        text-align: center;
    }
    &__btn{
        max-width: rem(260);
    }
    &__contbox{
        margin-top: rem(15);
        text-align: center;
    }
    &__continue{
        transition: all .2s ease;
        @media (any-hover: hover){
            &:hover {
              color: $accentColor;
            } 
        }
    }

}